import server from '../../server.js';
export default {
	data() {
		return {
			type:1,
			title:"安个利",
			upNameNumber:0,
			newName:''
		}
	},
	onLoad:function(val) {
		console.log(val.type)
		this.type=val.type;
		if(val.type=='1'){
			this.title="修改名字"
			this.getNumber();
		}
		if(val.type=='2'){
			this.title="个性签名"
		}
		if(val.type=='3'){
			this.title="等级特权"
		}
		if(val.type=='4'){
			this.title="关于安个利"
		}
		if(val.type=='5'){
			this.title="安个利隐私政策"
		}
		if(val.type=='6'){
			this.title="安个利用户协议"
		}
		if(val.type=='7'){
			this.title="关于安个利"
		}
		if(val.type=='8'){
			this.title="安个利充值协议"
		}
		if(val.type=='9'){
			this.title="会员协议"
		}
	},
	methods: {
		inputing:function(e){
			this.newName=e.detail.value;
			console.log(e.detail)
		},
		setName:function(){
			uni.showLoading({
				title: '正在提交数据'
			});
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/user.php?type=setName', //仅为示例，并非真实接口地址。
				data: {
					auid: server.userinfo.Auid,
					newName:this.newName,
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				success: (res) => {
					uni.showToast({
						title: res.data.msg,
						position:'bottom',
						icon:'none'
					})
					if(res.data.code=="1"){
						setTimeout(function () {
							uni.hideLoading()
							uni.navigateBack({
							    delta: 1
							});
						}, 1500);
					}
					
				},
				complete() {
					uni.hideLoading();
				}
			});
		},
		getNumber:function(){
			
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/user.php?type=getNameCount', //仅为示例，并非真实接口地址。
				data: {
					auid: server.userinfo.Auid,
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				success: (res) => {
					if(res.data.code=='1'){
						this.upNameNumber=res.data.data;
						console.log(this.upNameNumber,res.data.data)
					}
				}
			});
		},
		setms:function(){
			uni.showLoading({
				title: '正在提交数据'
			});
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/user.php?type=setms', //仅为示例，并非真实接口地址。
				data: {
					auid: server.userinfo.Auid,
					ms:this.newName,
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				success: (res) => {
					uni.showToast({
						title: res.data.msg,
						position:'bottom',
						icon:'none'
					})
					if(res.data.code=="1"){
						setTimeout(function () {
							uni.hideLoading()
							uni.navigateBack({
							    delta: 1
							});
						}, 1500);
					}
				},
				complete() {
					uni.hideLoading();
				}
			});
		}
	}
}
